body {
    margin-top: 20px;
    background: #eee; }
.white-bg {
    background-color: #ffffff; }
.page-heading {
    border-top: 0;
    padding: 0 10px 20px 10px; }
.forum-post-container {
    .media {
        margin: 10px 10px 10px 10px;
        padding: 20px 10px 20px 10px;
        border-bottom: 1px solid #f1f1f1; }
    .media-body {
        .photos {
            margin: 10px 0; } } }
.forum-avatar {
    float: left;
    margin-right: 20px;
    text-align: center;
    width: 110px;
    .img-circle {
        height: 48px;
        width: 48px; } }
.author-info {
    color: #676a6c;
    font-size: 11px;
    margin-top: 5px;
    text-align: center; }
.forum-post-info {
    padding: 9px 12px 6px 12px;
    background: #f9f9f9;
    border: 1px solid #f1f1f1; }
.media-body {
    & > .media {
        background: #f9f9f9;
        border-radius: 3px;
        border: 1px solid #f1f1f1;
        .forum-avatar {
            width: 70px;
            margin-right: 10px;
            .img-circle {
                height: 38px;
                width: 38px; } } } }
.forum-photo {
    max-width: 140px;
    border-radius: 3px; }
.mid-icon {
    font-size: 66px;
    font-size: 66px !important; }
.forum-item {
    margin: 10px 0;
    padding: 10px 0 20px;
    border-bottom: 1px solid #f1f1f1;
    small {
        color: #999; }
    .forum-sub-title {
        color: #999;
        margin-left: 50px; }
    &.active {
        .fa {
            color: #11101d; }
        a {
            &.forum-item-title {
                color: #11101d; } } } }
.views-number {
    font-size: 24px;
    line-height: 18px;
    font-weight: 400; }
.forum-container,
.forum-post-container {
    padding: 30px !important; }
.forum-title {
    margin: 15px 0 15px 0; }
.forum-info {
    text-align: center; }
.forum-desc {
    color: #999;
    float: none !important; }
.forum-icon {
    float: left;
    width: 30px;
    margin-right: 20px;
    text-align: center;
    .fa {
        font-size: 30px;
        margin-top: 8px;
        color: #9b9b9b; } }
a {
    &.forum-item-title {
        color: inherit;
        display: block;
        font-size: 18px;
        font-weight: 600;
        &:hover {
            color: inherit; } } }
@media (max-width:992px) {
    .forum-info {
        margin: 15px 0 10px 0;
        display: none; }

    .ibox {
        clear: both;
        margin-bottom: 25px;
        margin-top: 0;
        padding: 0; } }
.ibox {
    &.collapsed {
        .ibox-content {
            display: none; }
        .fa {
            &.fa-chevron-up {
                &:before {
                    content: "\f078"; } }
            &.fa-chevron-down {
                &:before {
                    content: "\f077"; } } } } }
.ibox:after,
.ibox:before {
    display: table; }
.ibox-title {
    -moz-border-bottom-colors: none;
    -moz-border-left-colors: none;
    -moz-border-right-colors: none;
    -moz-border-top-colors: none;
    background-color: #ffffff;
    border-color: #e7eaec;
    border-image: none;
    border-style: solid solid none;
    border-width: 3px 0 0;
    color: inherit;
    margin-bottom: 0;
    padding: 14px 15px 7px;
    min-height: 48px; }
.ibox-content {
    background-color: #ffffff;
    color: inherit;
    padding: 15px 20px 20px 20px;
    border-color: #e7eaec;
    border-image: none;
    border-style: solid solid none;
    border-width: 1px 0; }
.ibox-footer {
    color: inherit;
    border-top: 1px solid #e7eaec;
    font-size: 90%;
    background: #ffffff;
    padding: 10px 15px; }
.message-input {
    height: 90px !important; }
.form-control,
.single-line {
    background-color: #FFFFFF;
    background-image: none;
    border: 1px solid #e5e6e7;
    border-radius: 1px;
    color: inherit;
    display: block;
    padding: 6px 12px;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
    width: 100%;
    font-size: 14px; }
.text-navy {
    color: #1ab394; }
.m-b-sm {
    margin-bottom: 10px; }
.col-lg-12 {
    margin: 40px 0; }
.fa-globe {
    margin-right: 30px; }
